.searchContainer {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

.searchBar {
  background: rgba(255, 255, 255, 0.4);
  border: none;

  display: block;
  outline: none;
  width: 500px;
  height: 30px;
  padding: 10px;
  color: #333;

  box-shadow: 0 2px 10px 1px rgba(0, 0, 0, 0.5);
}

.searchBtn {
  width: 120px;
  margin-left: 30px;
  border-radius: 5px;
  border-style: hidden;
  background-color: burlywood;
}

.searchBtn:hover {
  background-color: bisque;
}
